import React, {useState} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {dispatchSortSelection} from './../../../pages/PLP/actions';
import Modal from 'react-modal';
import './SortModal.component.scss';

const customStyles = {
  content: {
    top: '20%',
    width: '93%',
    marginLeft: 'auto',
    marginRight: 'auto',
    background: 'rgb(255, 255, 255)',
    overflow: 'auto',
    borderRadius: '4px'
  }
};

const SortModal = ({closeModal, dispatchSortSelection, modalIsOpen}) => {

  const [sortType, setSortType] = useState('')
  const [selectionError, setSelectionError] = useState('')
  var subtitle;

  function afterOpenModal() {
    subtitle.style.color = '#000';
    subtitle.style.fontWeight = '700';
  }

  const submitSelection = (e) => {
    if (!sortType) {
      setSelectionError(true)
      return
    }
    dispatchSortSelection(sortType)
    closeModal(e)
    setSelectionError(false)
  }

  return (
    <Modal
      overlayClassName="c-SortModal modal fade show"
      closeTimeoutMS={300}
      className="modal-dialog"
      isOpen={modalIsOpen}
      onAfterOpen={afterOpenModal}
      onRequestClose={closeModal}
      style={customStyles}
      contentLabel="Example Modal"
    >
      <div className="modal-content">
        <div className="modal-header">
          <h5 className="modal-title" id="exampleModalLiveLabel" ref={_subtitle => (subtitle = _subtitle)}>Sort Options</h5>
          <button type="button" className="close" onClick={closeModal} aria-label="Close">
            <span aria-hidden="true">×</span>
          </button>
        </div>
        <div className="modal-body">
          {selectionError && <span className="error">Please Select One of the options below.</span>}
          <div className="container">
            <div className="radio">
              <label>
                <input type="radio" name="survey" checked={sortType === "priceHtoL"} onChange={() => {setSortType("priceHtoL"); setSelectionError(false)}} />Price -- High Low
              </label>
            </div>
            <div className="radio">
              <label>
                <input type="radio" name="survey" checked={sortType === "priceLtoH"} onChange={() => {setSortType("priceLtoH"); setSelectionError(false)}} />Price -- Low High
              </label>
            </div>
            <div className="radio disabled">
              <label>
                <input type="radio" name="survey" checked={sortType === "discount"} onChange={() => {setSortType("discount"); setSelectionError(false)}} />Discount
              </label>
            </div>
          </div>
        </div>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary" onClick={closeModal}>Cancel</button>
          <button type="button" className="btn btn-primary" onClick={submitSelection}>Apply</button>
        </div>
      </div>
    </Modal>
  );
};

SortModal.defaultProps = {

};

SortModal.propTypes = {
  closeModal: PropTypes.func,
  dispatchSortSelection: PropTypes.func,
  modalIsOpen: PropTypes.bool
};

const mapDispatchToProps = ({
  dispatchSortSelection
})

export default connect(
  null,
  mapDispatchToProps
)(SortModal);