import React, {useState} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {dispatchFilterRange} from './../../../pages/PLP/actions';
import Modal from 'react-modal';
import InputRange from 'react-input-range';
import './FilterModal.component.scss';
import 'react-input-range/lib/css/index.css';

const customStyles = {
  content: {
    top: '20%',
    width: '93%',
    marginLeft: 'auto',
    marginRight: 'auto',
    background: 'rgb(255, 255, 255)',
    overflow: 'auto',
    borderRadius: '4px'
  }
};

const FilterModal = ({closeModal, dispatchFilterRange, modalIsOpen}) => {

  const [filterRange, setFilterRange] = useState({min: 200, max: 5000})
  var subtitle;

  function afterOpenModal() {
    subtitle.style.color = '#000';
    subtitle.style.fontWeight = '700';
  }

  const submitSelection = (e) => {
    dispatchFilterRange(filterRange)
    closeModal(e)
  }

  return (
    <Modal
      overlayClassName="c-FilterModal modal fade show"
      closeTimeoutMS={300}
      className="modal-dialog"
      isOpen={modalIsOpen}
      onAfterOpen={afterOpenModal}
      onRequestClose={closeModal}
      style={customStyles}
      contentLabel="Example Modal"
    >
      <div className="modal-content">
        <div className="modal-header">
          <h5 className="modal-title" id="exampleModalLiveLabel" ref={_subtitle => (subtitle = _subtitle)}>Filter Options</h5>
          <button type="button" className="close" onClick={closeModal} aria-label="Close">
            <span aria-hidden="true">×</span>
          </button>
        </div>
        <div className="modal-body">
          <InputRange
            maxValue={10000}
            minValue={0}
            formatLabel={value => `${value} Kg`}
            value={filterRange}
            onChange={value => setFilterRange(value)} />
            {/* onChangeComplete={(e) =>submitSelection(e)} /> */}
        </div>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary" onClick={closeModal}>Cancel</button>
          <button type="button" className="btn btn-primary" onClick={submitSelection}>Apply</button>
        </div>
      </div>
    </Modal>
  );
};

FilterModal.defaultProps = {

};

FilterModal.propTypes = {
  closeModal: PropTypes.func,
  dispatchFilterSelection: PropTypes.func,
  modalIsOpen: PropTypes.bool
};

const mapDispatchToProps = ({
  dispatchFilterRange
})

export default connect(
  null,
  mapDispatchToProps
)(FilterModal);
